
  .teacher-item{
    background:var(--color-box-base);
    border: 1px solid var(--color-line-in-white);
    border-radius: 0.8rem;
    margin-top: 2.4rem;
    overflow: hidden;
  }

  .teacher-item header{
    padding: 3.2rem 2rem;
    display: flex;
    align-items: center;
  }

  .teacher-item header img{
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
  }

  .teacher-item header div{
    margin-left: 2.4rem;
  }

  .teacher-item header div strong{
    font: 700 2.4rem Archivo;
    display: block;
    color: var(--color-text-title);
  }

  .teacher-item header div span{
    font-size: 1.6rem;
    display: block;
    margin-top: 0.4rem;
  }

  .teacher-item > p{
    margin-left: 1.6rem;
    padding: 0.6rem;
    font-size: 1.6rem;
    line-height: 2.8rem;
  }
  
  .teacher-item footer{
    padding: 3.2rem 2rem;
    background-color: var(--color-box-footer);
    border-top: 1px solid var(--color-line-in-white);
    margin-top: 3.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .teacher-item footer p strong{
    color: var(--color-primary);
    font-size: 1.6rem;
    display: block;
  }

  .teacher-item footer button{
    width: 20rem;
    height: 5.6rem;
    background: var(--color-secundary);
    color: var(--color-button-text);
    border:0;
    border-radius: 0.8rem;
    cursor: pointer;
    font: 700 1.4rem Archivo;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    transition: 0.2s;
  }

  .teacher-item footer button:hover {
    background-color: var(--color-secundary-dark);
  }


  @media (min-width: 700px) {

    .teacher-item header,
    .teacher-item footer{
      padding:3.2rem;
    }

    .teacher-item > p{
      padding: 0 3.2rem;
    }

    .teacher-item footer p strong{
      display: initial;
      margin-left: 1.6rem;
    }

    .teacher-item footer bottom{
      width: 24.5rem;
      font-size: 1.6rem;
      justify-content: center;
    }

    .teacher-item footer button img{
      margin-right: 1.6rem;
    }
  }